import PropTypes from 'prop-types';

const ctx = require.context('../static/img', true, /\.(png|jpe?g|svg|webp)$/);

const Image = ({ src, alt }) => {
  const imagePath = src.replace('../static/img/', './');
  const props = {
    src: ctx(imagePath),
    alt: alt
  };
  const webpPath = imagePath.replace(/\.(png|jpe?g|svg)$/, '.webp');
  const webpSrc = ctx(webpPath);
  return (
    <picture>
      <source type="image/webp" srcSet={webpSrc} />
      <source type="image/jpeg" srcSet={props.src} />

      <img {...props} />
    </picture>
  );
};

Image.propTypes = {
  src: PropTypes.string.isRequired,
  alt: PropTypes.string.isRequired
};

export default Image;
